html {

}

body {
  background: #f2f2f2;
  font-family: 'Overpass', sans-serif;
}

.container {
  max-width: 1120px;
  margin: 0 auto;
}

img {
  max-width: 100%;
}

header {
  margin-top: 15px;
  .main-header {
    display: flex;
    flex-flow: row;
    align-items: center;
    justify-content: flex-end;
    border: solid 2px $black05;
    border-radius: 50px;
    .branding {
      width: 190px;
      padding-left: 15px;
      margin-right: auto;
      a {
        img {
          width: 100%;
        }
      }
    }
    .nav {
      ul {
        display: flex;
        flex-flow: row;
        margin-right: 25px;
        li {
          list-style-type: none;
          a {
            padding-left: 1em;
            padding-right: 1em;
            font-size: 14px;
            font-weight: 400;
            text-decoration: none;
            color: $textcolor-lite;
          }
        }
      }
    }
    .buy {
      width: 200px;
      padding-right: 10px;
      a {
        font-size: 14px;
        line-height: 14px;
        display: block;
        background: $button;
        border: solid 2px $bluelite;
        border-radius: 50px;
        padding: 12px 20px 10px 20px;
        color: $white;
        text-decoration: none;
      }
    }
  }
}
